import React, { useState } from "react";
import Plot from "./ScatterPlot.jsx";
import InputData from "./ContextInputData.jsx";
import LoadData from "./LoadData.jsx";
import Header from "./Header.jsx";
import Info from "./Info.jsx";
import {
    BrowserRouter as Router,
    Switch,
    Route,
} from "react-router-dom";
import { Container } from "reactstrap";

function Main() {
    const [jsonData, setJsonData] = useState({ input: '' });

    return (
        <>
            <InputData.Provider value={[jsonData, setJsonData]}>
                <Router>
                    <div className="App">
                        <Header />
                        <Switch>
                            <Route path="/analyse/">
                                <Plot />
                            </Route>
                            <Route path="/how-to-generate-data">
                                <Info />
                            </Route>
                            <Route path="/">
                                <LoadData />
                            </Route>
                        </Switch>
                    </div>
                </Router>
            </InputData.Provider>
        </>

    )
}

export default Main;